.App {
  position: relative;
  text-align: center;
}

#particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  /* Keep particles behind everything */
}

.hero-container {
  position: relative;
  z-index: 1;
  /* Content should be above the particles */
}

body::before,
body::after,
body .left-border,
body .right-border {
  content: none;
  /* Removing any generated content for borders */
}